<template lang="html">
  <b-container class="p-0 todo-item text-center m-0" :class="{'on_going': task.end_time == ''}">
    <b-row no-gutters  class="w-100">
      <b-col md="auto p-1" class="font-weight-bold">
        {{ task.id + 1}}.
      </b-col>
      <b-col class="font-weight-bold">
        {{  task.title  }}
      </b-col>
      <b-col v-if="task.duration" md="auto" class="p-2">
        {{ format_ms(task.duration)}}
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import moment from 'moment';
export default {
  name:"timer_element",
  props: ["task"],
  methods:{
    format_ms: function (time) {
       return moment.utc(time).format('HH:mm');
     },
  },
  data(){
    return {
      donig:"kala"
    }
  }
}
</script>

<style lang="css" scoped>
div{
  display: flex;
}

.on_going{
  background-color: #8bc34a;
}
</style>
